<template>
    <div class="list-wrapper">
        <div class="list-container">
            <template v-if="filteredEvents">
                <!-- <template v-for="(group, groupName) in groups">
                    <div class="list-date" :key="groupName + Math.random()">{{msToWeekDay(parseInt(groupName)) + ', ' + msToMonth(parseInt(groupName)) + ' ' + msToDay(parseInt(groupName))}}</div>
                        <div :key="item + Math.random()" v-for="item in group">
                            <div :class=" 'list-event ' + onlineAppointmentClassTag(item) + ' ' + assignedClassTag(item) + ' ' + availabilityClassTag(item)" @click="open(item)"
                                :key="item.OwnerId + Math.random()">
                                <div class="list-event-time">{{msToTime(item.localStoreStartDateTime) + ' - ' + msToTime(item.localStoreEndDateTime)}}</div>
                                <div class="list-event-name">{{item.accountName}}</div>
                                <div class="list-event-sa"><img class="list-owner-name-img" :src="getPictureUrl(item)" />{{item.ownerName}}</div>
                                <div class="list-event-status" style="width: 41px"></div>
                                 <div class="list-event-availability">{{item.freeCapacities + ' available slots'}}</div>
                            </div>
                        </div>
                </template> -->
                <template v-if="eventsMap">
                  <template v-for="date in Object.keys(eventsMap)">
                    <div class="list-date" :key="date + Math.random()">{{msToWeekDay(date) + ', ' + msToMonth(date) + ' ' + msToDay(date)}}</div>
                    <template v-for="(value, name) in eventsMap[date]">
                      <template v-for="(event,index) in value">
                        <span :key="event.id + Math.random()">
                            <div :class=" 'list-event ' + onlineAppointmentClassTag(event) + ' ' + assignedClassTag(event) + ' ' + availabilityClassTag(event)" @click="open(event)"
                                :key="event.OwnerId + Math.random()">
                                <div class="list-event-time">{{msToTime(event.localStoreStartDateTime) + ' - ' + msToTime(event.localStoreEndDateTime)}}</div>
                                <div class="list-event-name">{{event.accountName}}</div>
                                <div class="list-event-sa"><img class="list-owner-name-img" :src="getPictureUrl(event)" />{{event.ownerName}}</div>
                                <div class="list-event-status" style="width: 41px"></div>
                                <div class="list-event-availability">{{event.freeCapacities + ' available slots'}}</div>
                            </div>
                        </span>
                      </template>
                    </template>
                  </template>
                </template>
            </template>    
        </div>   
    </div>
</template>

<script>

export default {
  name: 'ListView',
  components: {
    
  },
  data () {
    return {

    }
  },
  methods:{
      open (event) {
      console.group('open',event);

      if(event.recordType == 'Availability'){
        this.$emit('showAvailToEdit', event);
      }
      else{
        this.$emit('showAssignModal', event);
      }
    },
    groupBy(array, key){
        const result = {}
        array.forEach(item => {
            if (!result[item[key]]){
            result[item[key]] = []
            }
            result[item[key]].push(item)
        })
        return result
    },
    // groupBy(array, key) {
    //   const result = {};
    //   let sortable = [];
    //   array.forEach(item => {
    //     if (!result[item[key]]) {
    //       result[item[key]] = [];
    //     }
    //     result[item[key]].push(item);
    //   });

    //   for (var i in result) {
    //     sortable.push([i, result[i]]);
    //   }
    //   sortable.sort(function(a, b) {
    //     return a[1] - b[1];
    //   });
    //   return sortable;
    // },
    msToDate(ms){
        var d = new Date(ms);
        // return d.getFullYear() + '-' + (d.getMonth().length > 1 ? d.getMonth() : '0' + d.getMonth()) + '-' + (d.getDate().length > 1 ? d.getDate() : '0' + d.getDate());
        return d.getFullYear() + '-' + ("0" + (d.getMonth() + 1)).slice(-2) + '-' + ("0" + d.getDate()).slice(-2);
    },
    msToTime(ms){
      var d = new Date(ms);
      return d.getHours() + ':00';
    },
    msToWeekDay(ms){
      var d = new Date(ms);
      let realDate = new Date(ms).toLocaleString('en-us', {weekday: 'short'});
      return realDate;
    },
    msToMonth(ms){
      var d = new Date(ms);
      let realDate = new Date(ms).toLocaleString('en-us', {month: 'short'});
      return realDate;
    },
    msToDay(ms){
      var d = new Date(ms);
      return d.getDate();
    },
    onlineAppointmentClassTag(item){
      if (item.recordType == "Online_Appointment"){
        return "online-appointment-tag"
      }
      if (item.recordType == "Appointments"){
        return "appointment-tag"
      }
    },
    assignedClassTag(item){
      if (item.status == "Assigned"){
        return "assigned-tag"
      }
      if(item.status == "Not Assigned"){
        return "unassigned-tag"
      }
      if(item.status == "Cancelled"){
        return "cancelled-tag"
      }
    },
    availabilityClassTag(item){
      if (item.recordType == "Availability"){
        return "availability-tag"
      }
      else{
        return "not-availability-tag"
      }
    },
    getPictureUrl(item){
        let filterUser = this.availableTeamUsers.filter(ev => ev.Id == item.ownerId)
        let userUrl = '';
        if(filterUser[0])
          userUrl = filterUser[0].FullPhotoUrl
        return userUrl
    },
   
    arrayUnique(array) {
        var a = array.concat();
        for(var i=0; i<a.length; ++i) {
            for(var j=i+1; j<a.length; ++j) {
                if(a[i] === a[j])
                    a.splice(j--, 1);
            }
        }
        return a;
    },
  },
  computed: {
    events(){
        return this.$store.state.events
    },
    availableTeamUsers(){
        return this.$store.state.availableTeamUsers;
    },
    groups(){
        return this.groupBy(this.filteredEvents, 'localStoreStartDateTime')
    },
    selectedEventsFilters(){
        return this.$store.state.selectedEventsFilters;
    },
     filteredEvents(){
        if(this.selectedEventsFilters.indexOf('All') != -1){
            return this.events;
        } else{
            let filteredEventsArr = [];
            for(var i = 0; i<this.selectedEventsFilters.length;i++){
            if(this.selectedEventsFilters[i] == "Available"){
                filteredEventsArr = this.arrayUnique(filteredEventsArr.concat(filteredEventsArr, this.events.filter(event => event.recordType == "Availability" && event.freeCapacities > 0)));
            }
            if(this.selectedEventsFilters[i] == "Unassigned"){
                filteredEventsArr = this.arrayUnique(filteredEventsArr.concat(filteredEventsArr, this.events.filter(event => event.recordType == "Online_Appointment" && event.status == "Not Assigned")));
            }
            if(this.selectedEventsFilters[i] == "Assigned"){
                filteredEventsArr = this.arrayUnique(filteredEventsArr.concat(filteredEventsArr, this.events.filter(event => event.recordType == "Online_Appointment" && event.status == "Assigned")));
            }
            if(this.selectedEventsFilters[i] == "ICON"){
                filteredEventsArr = this.arrayUnique(filteredEventsArr.concat(filteredEventsArr, this.events.filter(event => event.recordType == "Appointments")));
            }
            }
            return filteredEventsArr;
        }
    },
    // actualDateRangeToShow(){
    //   let actualDateRange = this.$store.state.actualDateRange
    //   return actualDateRangeToShow
    // },
    // getListDateHeaders(){
    //  let actualDateRange = this.$store.state.actualDateRange;
    // //  let startDateObj = new Date(actualDateRange.start);
    //  let getListDateHeaders = [];
    //  for (let index = 0; index < 8; index++) {
    //     let startDateObj = new Date(actualDateRange.start);
    //     if (index != 0 ){
    //     startDateObj.setDate(startDateObj.getDate() + index) ;
    //     }
    //     let day = startDateObj.toLocaleString('en-us', {  weekday: 'short' });
    //     let month = startDateObj.toLocaleString('en-us', {  month: 'short' });
    //     let dayNum = startDateObj.getDate();
    //     getListDateHeaders.push(day + ', ' + month + ' ' + dayNum );
    //  }
    //  return getListDateHeaders;
    // },
    eventsMap () {
        if(this.filteredEvents.length > 0){
          let group = this.filteredEvents.reduce((r, a) => {
          var d = new Date();
          if(a.localStoreStartDateTime){
            d = new Date(a.localStoreStartDateTime);
          } else if(a.gmtStartDateTime){
            d = new Date(a.gmtStartDateTime);
          }

            let tempDate = d.getFullYear() + '-' + ((d.getMonth()+1) > 9 ? (d.getMonth() + 1) : '0' + (d.getMonth() + 1)) + '-' + (d.getDate() > 9 ? d.getDate() : '0' + d.getDate());
            let tempHour = d.getHours() + ':00';
            r[tempDate] = [...r[tempDate] || [], a];
            return r;
            }, {});

            let group2 = {}
            Object.keys(group).forEach(key=>{
              group2[key] = group[key].reduce((r, a) => {
            

            var d = new Date();
            if(a.localStoreStartDateTime){
              d = new Date(a.localStoreStartDateTime);
            } else if(a.gmtStartDateTime){
              d = new Date(a.gmtStartDateTime);
            }

            let tempDate = d.getFullYear() + '-' + ((d.getMonth()+1) > 9 ? (d.getMonth() + 1) : '0' + (d.getMonth() + 1)) + '-' + (d.getDate() > 9 ? d.getDate() : '0' + d.getDate());
            let tempHour = d.getHours() + ':00';
            r[tempHour] = [...r[tempHour] || [], a];
            return r;
            }, {});
            })

          console.log("*************MAPEVENTS******************")
          console.log(group2)
          this.$store.dispatch('setEventsMap', group2)
          return group2
        }
        
      },
  }
}
</script>
<style>

.list-wrapper{
    padding: 12px 43px 19px 29px;
    background-color: #f7f8fb;
    overflow-y: auto;
    height: 84vh;
}
  
.list-container{
    display: flex;
    flex-direction: column;
    margin-bottom: 37px
}

.list-date{
    font-size: 16px;
    color: #000000;
    margin-top: 20px;
    margin-bottom: 14px;
    text-transform: uppercase;
    font-weight: 600;
}

.list-event{
    border-radius: 7px;
    box-shadow: 1px 2px 4px 0 rgba(0, 0, 0, 0.09);
    background-color: #ffffff;
    white-space: nowrap;
    font-size: 13px;
    cursor: pointer;
    display: flex;
    padding: 12px 30px;
    align-items: center;
    margin-bottom: 12px;
    justify-content: space-between;
}

.list-event-time{
    font-size: 14px;
    font-weight: 500;
    color: #768191;
    max-width: 100px;
}

.list-event-name{
    font-size: 16px;
    color: #2e3a49;
    max-width: 200px;
    width: 200px;
    overflow: hidden;
    text-overflow: ellipsis;
}

.list-event-sa{
    opacity: 0.81;
    font-size: 16px;
    font-weight: 400;
    color: #000000;
    max-width: 200px;
    width: 200px;
    overflow: hidden;
    text-overflow: ellipsis;
}

.list-event-status::before{
    content: 'ONLINE';
    border-radius: 2px;
    background-color: #7a91ff;
    color: #ffffff;
    font-size: 8px;
    font-weight: 500;
    text-align: center;
    text-transform: uppercase;
    padding: 2px 4px;
    letter-spacing: 1px;
    display: inline;
}

.list-event-empty{
    border-radius: 7px;
    background-color: #f1f2f9;
    color: #797979;
    font-size: 16px;
    text-transform: uppercase;
    padding: 12px;
    text-align: center
}

.event-unassigned{
    border-top: dashed 1px #e30041;
    border-right: dashed 1px #e30041;
    border-bottom: dashed 1px #e30041;
    border-left: 6px #e30041 solid;
}

.availability-tag{
  background-color: #ebeefd;
    color: #54698d;
    font-size: 13px;
    border-left: 6px #c2cbf5 solid;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
}

.list-owner-name-img{
    min-width: 20px;
    max-width: 20px;
    height: 20px;
    margin-bottom: -5px;
    margin-right: 12px;
    border-radius: 100%;
}

.list-event-availability{
    font-size: 16px;
}

.availability-tag .list-event-name, .availability-tag .list-event-sa, .availability-tag .list-event-status, .availability-tag .list-event-time{
  display: none;
}

.not-availability-tag .list-event-availability{
  display: none;
}

.appointment-tag .list-event-status::before{
    display: none;
}

@media screen and (max-width: 1024px) {
  .list-wrapper{
        height: 73.5vh;
  }
}

</style>


