<template>

  <div class="leftMenu-wrapper" :class="{Transition: isTransition}">

    <div class="leftMenu" >
        <img class="storeImage"  src="@/assets/lv-champs-elysees.jpg" />
        <span class="storeName">{{storeInfos.Name ? storeInfos.Name : 'LV CHAMPS-ELYSEES'}}</span>
        
        <hr class="hrSeparator" />

        <div class="today-date">
          <div class="today-weekday">{{todayWeekday}}</div>
          <div class="middle-date">
            <div>{{todayDay}}</div>&nbsp;
            <div>{{todayMonth}}</div>
          </div>
          <div class="today-year">{{todayYear}}</div>

          <div class="appointments-num">
            Hello, <span style="font-weight: 600">{{userInfos.FirstName ? userInfos.FirstName : ''}}!</span> 
            You have <span style="font-weight: 600">{{unassignedInMonth ? unassignedInMonth.length : 0}}</span><br> unassigned appointments
          </div>

        </div>

        <hr class="hrSeparator" />

        <template >
          <div justify="center" color="black" class="date-picker">
            <v-date-picker v-model="datePicker" color="black" width="100%" @change="miniCalChange" :events="unassignedInMonth" :event-color="'red'"></v-date-picker>
          </div>
        </template>

    </div>
    
     <div class="back-btn-container" >
      <v-btn
        absolute
        class="back-btn"
        @click="makeTransition" 
        >
         <v-icon size="20px" v-if="!isTransition">arrow_back</v-icon>
         <v-icon size="20px" v-if="isTransition">arrow_forward</v-icon>
       </v-btn>
       <v-btn
        absolute
        class="refresh-btn"
        @click="reloadEvents"
        >
         <v-icon size="20px">refresh</v-icon>
       </v-btn>
    </div>

  </div>
</template>

<script>
import utils from '../commons/utils'
export default {
  name: 'LeftMenu',
  components: {
    
  },
  data () {
    return {
      todayDay : new Date().getDate(),
      todayYear : new Date().getFullYear(),
      isHidden: false,
      isTransition: false,
      datePicker: '',
      images: {
        sample: require('../assets/lv-champs-elysees.jpg')
      },
      miniCalEvents: [],
    }
  },
  computed: {
    todayWeekday(){
      let date = new Date();
      let realDate = new Date().toLocaleString('en-us', {  weekday: 'long' });
      return realDate
    },
    todayMonth(){
      let date = new Date();
      let realMonth = new Date().toLocaleString('en-us', {  month: 'long' });
      return realMonth
    },
    storeInfos(){
      return this.$store.state.storeInfos;
    },
    userInfos(){
      return this.$store.state.userInfos;
    },
    unassignedInMonth(){
      let unassignedEvents = [];
      for(var i=0; i<this.$store.state.events.length; i++){
        if(this.$store.state.events[i].status == 'Not Assigned'){
          unassignedEvents.push(this.msToDate(this.$store.state.events[i].localStoreStartDateTime));
        }
      }
      return unassignedEvents;
      
      // let unassignedEvents = this.$store.state.unassignedEventsInMonth;
      // let datesArray = []
      // for(var i=0; i<unassignedEvents.length; i++){
      //   if(unassignedEvents[i].status == 'Unassigned' || unassignedEvents[i].status == 'Not Assigned'){
      //     datesArray.push(this.msToDate(unassignedEvents[i].localStoreStartDateTime));
      //   }
      // }
      // return datesArray;
    },
  },
  methods:{
    miniCalChange(e){
      console.log(e)
      let actualRange= {};

      actualRange.start = e;
      actualRange.end = e;
      actualRange.startDisplayString = this.dateToDisplayString(new Date(e.split('-')[0], parseInt(e.split('-')[1]) - 1, e.split('-')[2]));
      actualRange.endDisplayString = this.dateToDisplayString(new Date(e.split('-')[0], parseInt(e.split('-')[1]) - 1, e.split('-')[2]));
      actualRange.year = e.split('-')[0];

      this.$store.dispatch('setCalendarStart', e);   

      //this.$store.dispatch('updateCalendarViewType', 'day');   
      this.$store.dispatch('updateActualRange', actualRange);
      utils.getDateRangeAvailabilities()
    },
    dateToDisplayString(date){
      let month = date.toLocaleString('default', { month: 'long' });
      return month + ' ' + date.getDate()
    },
    msToDate(ms) {
        var d = new Date(ms);
        return d.getFullYear() + '-' + ("0" + (d.getMonth() + 1)).slice(-2) + '-' + ("0" + d.getDate()).slice(-2);
    },
    makeTransition: function() {
      this.isTransition = !this.isTransition;
    },
    reloadEvents(){
      this.$store.dispatch('emptyEvents');
      utils.getDateRangeAvailabilities();
      utils.getOnlineAndTeamAppointments('month');
    },
  }
}
</script>
<style>

  @import url('../assets/LVFont/font-faces.css');

  .application{
    font-family: 'Louis Vuitton';
  }

  .Transition{
    margin-left: -282px;
  }

  .leftMenu-wrapper {
    transition: all 500ms;
    display: flex;
    width: 323px;
    box-shadow: 19px 2px 22px -9px rgba(25, 17, 11, 0.13);
    border-right: solid 1px #eae8e4;
    z-index: 40;
    text-align: center;
    background-color: #ffffff;
}

.back-btn-container {
    right: 41px;
    height: auto;
    box-shadow: none;
    border: none;
    transition: all 500ms;
    position: relative;
}

.leftMenu {
    width: auto;
    padding: 0 41px;
    float: none;
    display: flex;
    flex-direction: column;
}

/*  */
  /* .leftMenu-wrapper{
    height: 100%;
    float: left;
    display: flex;
    position: relative;
  } */

/*  */
  /* .leftMenu{
    float: left;
    height: 100%;
    width: 282px;
    text-align: center;
    background-color: #ffffff;
    z-index: 40;
    padding: 0 0 0 41px;
    display: flex;
    flex-direction: column;
  } */

/*  */
  /* .back-btn-container{
    border-right: solid 1px #eae8e4;
    height: 100%;
    background: white;
    width: 41px;
    z-index: 40;
    position: absolute;
    box-shadow: 19px 2px 22px -9px rgba(25, 17, 11, 0.13);
    right: -41px;
  } */

  .back-btn{
    height: 41px;
    width: 40px;
    min-width: 35px;
    padding: 0;
    border-left: solid 1px #e6e6e6;
    border-bottom: solid 1px #e6e6e6;
    background-color: #ffffff !important;
    box-shadow: none !important;
  }

  .refresh-btn{
    top: 41px;
    height: 41px;
    width: 40px;
    min-width: 35px;
    padding: 0;
    border-left: solid 1px #e6e6e6;
    border-bottom: solid 1px #e6e6e6;
    background-color: #ffffff !important;
    box-shadow: none !important;
  }
  .storeImage{
    /* background-image: url('../assets/lv-champs-elysees.jpg'); */
    width: 71px;
    height: 71px;
    background-size: cover;
    border-radius: 50px;
    margin: auto;
    margin-bottom: 20px;
    margin-top: 44px;
  }
  .storeName{
    font-weight: bold;
    font-size: 18px;
    margin-bottom: 15px;
  }
  .hrSeparator{
    width: 100%;
    opacity: 0.19;
    border: solid 1px #979797;
    margin: auto;
    margin-bottom: 28px;
    margin-top: 32px;
  }

  .today-year{
    text-align: left
  }

  .today-weekday{
    font-size: 24px;
    font-weight: 300;
    color: #28211c;
    text-align: left
  }

  .middle-date{
    display: flex;
    font-size: 36px;
    color: #19110b;
    font-weight: 600;
    margin-top: -7px;
  }

  .today-year{
    font-size: 13px;
    color: #28211c;
  }

  .appointments-num{
    margin-top: 18px;
    font-size: 17px;
    font-weight: 300;
    line-height: 1.41;
    color: #000000;
    text-align: left
  }
  
  .v-picker__title{
    display: none;
  }

  .v-picker.v-card{
    box-shadow: none;
  }
  
  .v-picker .v-date-picker-table th {
    font-size: 14px;
  }

  button.v-btn.v-btn--flat.v-btn--floating.v-btn--outline.theme--light.black--text {
    background-color: #000000 !important;
  }

  button.v-btn.v-btn--flat.v-btn--floating.v-btn--outline.theme--light.black--text .v-btn__content {
    color: #ffffff
  }

  .date-picker{
    width: 110%;
    margin-left: -12px;
  }

  .v-date-picker-table__events>div {
    border-radius: 50%;
    display: inline-block;
    height: 6px;
    margin: 0 1px 2px;
    width: 6px;
  }

@media screen and (max-width: 1024px) {
   .leftMenu-wrapper {
    display: flex;
    width: 240px;
   }

   .Transition{
    margin-left: -210px;
    }

   .leftMenu {
    padding: 0 30px;
   }

   .back-btn-container {
    right: 30px;
   }

   .back-btn, .refresh-btn {
    height: 30px;
    width: 30px;
    min-width: 30px;
   }

   .refresh-btn {
    top: 30px;
   }

    .middle-date {
      font-size: 31px;
    }

    .date-picker{
      width: 115%;
      position: relative;
      left: -2px;
    }

    .v-date-picker-table--date .v-btn {
        height: 25px;
        width: 25px;
    }

    .v-date-picker-table__events>div {
      height: 5px;
      margin: 0px 1px 1px;
      width: 5px;
    }

  }

</style>


