        text-align: left;
        padding-left: 15px !important;
        padding-top: 25px !important;
    }

    .week-day-wrapper{
        display: flex;
        flex-wrap: nowrap
    }

    .week-day-wrapper::before{
        content: "Repeat on";
        position: absolute;
        top: 43%;
        color: #8c9199;
    }

    .radio-ends::before{
        content: "Ends";
        position: absolute;
        top: 58%;
        font-size: 14px;
        color: #8c9199;
    }

    .Reccurence-select {
    margin-right: 10px;
    text-align: left;
    }

    /* .Reccurence-select::before{
        content: "Repeat every";
        position: absolute;
        top:43%;
        color: #8c9199;
    } */

    .SelectDay{
        background-color: #d9d7d2;
        color: #000000
    }

    .v-radio {
        min-width: 57px;
    }

    .radio-ends .v-input__slot{
        padding-left: 0;
        background: none
    }

    .radio-ends-row{
        display: flex;
        height: 34px;
    }

    .radio-ends-row .v-input__slot{
        background-color: #f6f5f3;
        padding-left: 19px;
        min-height: 40px
    }

    .radio-ends-row .v-text-field{
        margin-top: -20px;
    }


    .radio-input .v-input__slot{
        width: 43px;
        padding-left: 12px;
    }

    label.v-label {
        color: #000000 !important;
        font-size: 17px;
    }

    /* .toLabel{
        margin: auto;
        opacity: 0.5;
        font-size: 18px;
    } */

    label.v-label {
        color: #000000 !important;
        font-size: 17px;
    }

    .toLabel-assign{
        margin: auto 18px auto -18px;
        opacity: 0.5;
        font-size: 18px;
    }

    .disabled-input .v-input__slot{
        background-color:#ffffff
    }

    .disabled-input .v-input__append-inner {
        display: none;
    }

    .disabled-input .v-select__selection--disabled, .disabled-input input{
        color: black !important;
        font-weight: 500;
    }

    .disabled-input .v-input__slot{
        padding: 0
    }

    .lds-spinner {
    color: official;
    /* display: inline-block; */
    position: relative;
    bottom: 16px;
    /* width: 80px;
    height: 80px; */
    }
    .lds-spinner div {
    transform-origin: 15px 17px;
    animation: lds-spinner 1.2s linear infinite;
    }
    .lds-spinner div:after {
    content: " ";
    display: block;
    position: absolute;
    top: 3px;
    left: 14px;
    width: 2px;
    height: 9px;
    border-radius: 20%;
    background: #fff;
    }
    .lds-spinner div:nth-child(1) {
    transform: rotate(0deg);
    animation-delay: -1.1s;
    }
    .lds-spinner div:nth-child(2) {
    transform: rotate(30deg);
    animation-delay: -1s;
    }
    .lds-spinner div:nth-child(3) {
    transform: rotate(60deg);
    animation-delay: -0.9s;
    }
    .lds-spinner div:nth-child(4) {
    transform: rotate(90deg);
    animation-delay: -0.8s;
    }
    .lds-spinner div:nth-child(5) {
    transform: rotate(120deg);
    animation-delay: -0.7s;
    }
    .lds-spinner div:nth-child(6) {
    transform: rotate(150deg);
    animation-delay: -0.6s;
    }
    .lds-spinner div:nth-child(7) {
    transform: rotate(180deg);
    animation-delay: -0.5s;
    }
    .lds-spinner div:nth-child(8) {
    transform: rotate(210deg);
    animation-delay: -0.4s;
    }
    .lds-spinner div:nth-child(9) {
    transform: rotate(240deg);
    animation-delay: -0.3s;
    }
    .lds-spinner div:nth-child(10) {
    transform: rotate(270deg);
    animation-delay: -0.2s;
    }
    .lds-spinner div:nth-child(11) {
    transform: rotate(300deg);
    animation-delay: -0.1s;
    }
    .lds-spinner div:nth-child(12) {
    transform: rotate(330deg);
    animation-delay: 0s;
    }
    @keyframes lds-spinner {
    0% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
    }


    @media screen and (max-width: 1024px) {
      .editEventModal .v--modal-box.v--modal{
            min-width: 510px;
            top: 60px !important;
            margin: auto !important;
            height: auto !important;
            left: 0 !important;
      }

      .editEventModalContent {
            padding: 32px 21px 32px 38px;
      }
      
    }
</style>


