<template>
  <v-app>
    <v-content>
      <LeftMenu />
      <div class="horizontal-wrapper">
        <HeaderBar />
        <UsersSelection />
        <Calendar class="calendar-wrapper" ref="calendarComponent" v-if="!isHidden"/>
        <div class="calendar-footer">
          <div class="calendar-footer-left">Canceled Appointment</div>
          <div class="calendar-footer-center"><img class="calendar-footer-center-img" src="@/assets/noun-show-1517840.png" />No Show</div>
          <div class="calendar-footer-right"><img class="calendar-footer-right-img" src="@/assets/noun-shop-bag-2664616.png" />Purchase</div>
        </div>  
      </div> 
      <onBoardingModal @close="hideOnboarding()" />
    </v-content>
  </v-app>
</template>

<script>
import '@mdi/font/css/materialdesignicons.css'
import Vue from 'vue'
import Vuetify from 'vuetify/lib'
import SideBar from './components/SideBar'
import LeftMenu from './components/LeftMenu'
import HeaderBar from './components/HeaderBar'
import UsersSelection from './components/UsersSelection'
import Calendar from './components/Calendar'
import utils from './commons/utils'
import LCC from 'lightning-container'
import onBoardingModal from './components/onBoardingModal';
import { timeout } from 'q'
import VueRamda from 'vue-ramda'

Vue.use(VueRamda)
Vue.use(Vuetify)

export default {
  name: 'App',
  components: {
    Calendar,
    SideBar,
    LeftMenu,
    HeaderBar,
    UsersSelection,
    LCC,
    onBoardingModal
  },
  icons: {
    iconfont: 'mdi', 
  },
  data () {
    return {
      showOnBoarding: true,
      isHidden: false,
      // searchQuery: 'Nathaniel',
      // nowTimeMS: new Date().getTime(),
      // dummyEvents: [
      //    {
      //       "definedCapacities":4,
      //       "DurationInMinutes":60,
      //       "freeCapacities":4,
      //       "gmtEndDateTime":this.nowTimeMS + 3600000,
      //       "gmtStartDateTime":this.nowTimeMS,
      //       "id":"00U0t000003BrfDEAS",
      //       "IsRecurrence":false,
      //       "localStoreEndDateTime":this.nowTimeMS + 3600000,
      //       "localStoreStartDateTime":this.nowTimeMS,
      //       "recordType":"Availability",
      //       "storeCode":"A06",
      //       "storeName":"LV PARIS CHAMPS ELYSEES",
      //       "timeZone":"Europe/Paris"
      //    },
      // ]
    }
  },
  methods:{
    hideOnboarding(){
      this.$modal.hide('onBoardingModal');
    }
  },
  mounted: async function(){
    //utils.getStoreUsersAvailabilities();

    utils.getStoreInfos();
    utils.getStoreUsers();
    utils.getUserInfos();
    utils.getDateRangeAvailabilities();
    utils.getOnlineAndTeamAppointments('month');
    this.$modal.show('onBoardingModal');    
  },
}
</script>

<style>
html{
  overflow: hidden;
}

/*  */
.v-content__wrap{
  display: flex;
}
/*  */
.horizontal-wrapper {
    flex-grow: 1;
    height: auto;
}

/*  */
/* .horizontal-wrapper{
  height: 101vh;
} */

.calendar-wrapper{
  height: 84vh;
  /* overflow-x: auto; */
}

button.floating-btn {
    margin: 74px 36px;
    height: 66px;
    width: 66px;
    box-shadow: 0 8px 8px 0 rgba(0, 0, 0, 0.24), 0 0 8px 0 rgba(0, 0, 0, 0.12) !important;
    border-style: solid;
    border-width: 0.5px;
    border-image-source: linear-gradient(to bottom, rgba(255, 255, 255, 0.12), rgba(255, 255, 255, 0.06) 20%, rgba(255, 255, 255, 0));
    border-image-slice: 1;
    bottom: 0 !important;
}

.calendar-footer{
    display: inline-flex;
    margin-top: 5px;
    backdrop-filter: blur(10px);
    box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.1);
    bottom: 0;
    width: 100%;
    position: fixed;
    align-items: center;
    justify-content: start;
    padding: 15px 0 15px 5px;
    z-index: 50;
    margin-left: 0;
}

.calendar-footer-left,
.calendar-footer-center,
.calendar-footer-right{
  font-size: 15px;
  line-height: 0.6;
  color: #2e3a49;
  cursor: pointer;
  vertical-align: sub;
  margin-left: 58px;
}

.calendar-footer-left::before{
  background-image: linear-gradient(45deg, #d3d3d3 16.67%, #fff0f0 16.67%, #fff0f0 50%, #d3d3d3 50%, #d3d3d3 66.67%, #fff0f0 66.67%, #fff0f0 100%);
  background-size: 4.24px 4.24px;
  width: 18px;
  height: 18px;
  border: solid 1px #d3d3d3;
  border-radius: 100%;
  content: '';
  display: inline-block;
  margin-right: 14px;
}

.calendar-footer-center, .calendar-footer-right {
    display: flex;
    align-items: center;
}

.calendar-footer-right-img, .calendar-footer-center-img{
  margin-right: 15px;
}


@media screen and (max-width: 1024px) {
  .modal-container {
    width: 434px !important;
    padding: 25px 25px 35px !important;
  }
}

@media screen and (max-height: 768px) {
  .calendar-wrapper{
        height: 73.5vh;
  }
}


</style>




