<script>
import moment from 'moment';
import LCC from 'lightning-container';
import store from "../store";
import VueRamda from "vue-ramda";

export default {
    log(s){
        console.log(s)
    },
    getDateRangeAvailabilities(){
       if (process.env.NODE_ENV === 'production') {

          var startDateRange = moment().year() + '-' + moment().format("MM") + '-01';
          var startDateRangeM = moment(startDateRange).subtract(7, 'days').format('YYYY-MM-DD');

          var endDateRange;
          if(moment().month() == 11){
            endDateRange = (moment().year() + 1) + '-01-31';
          } else {
            endDateRange = moment().year() + '-' + moment().format("MM") + '-' + (moment().daysInMonth().toString().length > 1 ? moment().daysInMonth() : '0' + moment().daysInMonth()) ;
          }
          
          let obj = {onlyOnline: false, onlyUnassigned: false, datesString: '', startDate: startDateRangeM, endDate: endDateRange};
          let params = JSON.stringify(obj);


          // let actualDateRange = store.state.actualDateRange;
          // let params = JSON.stringify(actualDateRange);


              LCC.callApex(
                    'OnlineAppointmentsController.getAvailabilities',
                    params,
                    (result, event) => {
                    if (event.status) {
                        this.handleLCCQueryResponse(result)
                    } else if (event.type === 'exception') {
                        console.log(event.message + ' : ' + event.where)
                    } else {
                        console.log('Unknown Error', event)
                    }
                    },
                    {escape: false}
                )
        } else {
          console.log('not in production')
        }
    },
    getOnlineAndTeamAppointments(type){
       if (process.env.NODE_ENV === 'production') {
          let obj = {};

          if(type == 'range'){
            obj = {onlyOnline: false, onlyUnassigned: false, datesString: '', startDate: store.state.actualDateRange.start, endDate: store.state.actualDateRange.end}
          } else {
            //calculate first and last day of the month to get all appointments in the whole current month

            var startDateRange = moment().year() + '-' + moment().format("MM") + '-01';
            var startDateRangeM = moment(startDateRange).subtract(7, 'days').format('YYYY-MM-DD');

            var endDateRange;
            if(moment().month() == 11){
              endDateRange = (moment().year() + 1) + '-01-31';
            } else {
              endDateRange = moment().year() + '-' + moment().format("MM") + '-' + (moment().daysInMonth().toString().length > 1 ? moment().daysInMonth() : '0' + moment().daysInMonth());
            }
            
            obj = {onlyOnline: false, onlyUnassigned: false, datesString: '', startDate: startDateRangeM, endDate: endDateRange};
          }

            let params = JSON.stringify(obj);
            
            console.log('getOnlineAndTeamAppointments obj store.state.actualDateRange', store.state.actualDateRange)
            console.log('getOnlineAndTeamAppointments obj params', params)


              LCC.callApex(
                'OnlineAppointmentsController.getOnlineAndTeamAppointments',
                params,
                (result, event) => {
                  if (event.status) {
                      console.log('SUCCESS TEST: ' , result)
                      this.handleLCCQueryResponse(result);
                      return result;
                  } else if (event.type === 'exception') {
                      console.log(event.message + ' : ' + event.where)
                  } else {
                      console.log('Unknown Error', event)
                  }
                },
                {escape: false}
            )
        } else {
          console.log('not in production')
        }
    },
    getUserInfos(){
      if (process.env.NODE_ENV === 'production') {
          LCC.callApex(
            'OnlineAppointmentsController.getUserInfos',
            (result, event) => {
              if (event.status) {
                console.log('UserInfos: ' , result)
                store.dispatch('setUserInfos', result);
                return result;
              } else if (event.type === 'exception') {
                console.log(event.message + ' : ' + event.where)
              } else {
                console.log('Unknown Error', event)
              }
            },
            {escape: false}
          )
        } else {
          console.log('not in production')
        }
    },
    getAvailableTeamUsers(){
      if (process.env.NODE_ENV === 'production') {
         let actualDateRange = store.state.actualDateRange;
         let params = '{"startRange": ' + JSON.stringify(actualDateRange.start) + ', "endRange": ' + JSON.stringify(actualDateRange.end) + '}';

          LCC.callApex(
            'OnlineAppointmentsController.getAvailableTeamUsers',
            params,
            (result, event) => {
              if (event.status) {
                console.log('getAvailableTeamUsers: ' , result)
                store.dispatch('setAvailableTeamUsers', result);
                return result;
              } else if (event.type === 'exception') {
                console.log(event.message + ' : ' + event.where)
              } else {
                console.log('Unknown Error', event)
              }
            },
            {escape: false}
          )
        } else {
          console.log('not in production')
        }
    },
    getStoreUsers(){
      if (process.env.NODE_ENV === 'production') {
          LCC.callApex(
            'OnlineAppointmentsController.getStoreUsers',
            (result, event) => {
              if (event.status) {
                console.log('getAvailableTeamUsers: ' , result)
                store.dispatch('setAvailableTeamUsers', result);
                return result;
              } else if (event.type === 'exception') {
                console.log(event.message + ' : ' + event.where)
              } else {
                console.log('Unknown Error', event)
              }
            },
            {escape: false}
          )
        } else {
          console.log('not in production')
        }
    },
    getStoreInfos(){
       if (process.env.NODE_ENV === 'production') {
          LCC.callApex(
            'OnlineAppointmentsController.getStoreInfos',
            (result, event) => {
              if (event.status) {
                console.log('StoreInfos: ' , result)
                store.dispatch('setStoreInfos', result)
                return result;
              } else if (event.type === 'exception') {
                console.log(event.message + ' : ' + event.where)
              } else {
                console.log('Unknown Error', event)
              }
            },
            {escape: false}
          )
        } else {
          console.log('not in production')
        }
    }, 
    assignAppointments(userId, onlineAppointmentId, reassignFromManager){
        if (process.env.NODE_ENV === 'production') {

          let params = '{"userId": "' + userId + '", "onlineAppointmentId": "' + onlineAppointmentId + '", "reassignFromManager": "' + reassignFromManager + '"}';

              LCC.callApex(
                    'OnlineAppointmentsController.assignAppointments',
                    params,
                    (result, event) => {
                    if (event.status) {
                        console.log('assignAppointments', result);
                        store.dispatch('emptyEvents');
                        utils.getDateRangeAvailabilities();
                        utils.getOnlineAndTeamAppointments('month');
                    } else if (event.type === 'exception') {
                        console.log(event.message + ' : ' + event.where)
                    } else {
                        console.log('Unknown Error', event)
                    }
                    },
                    {escape: false}
                )
        } else {
            console.log('not in production')
        }
    },
    handleLCCQueryResponse(response) {
      console.log(response)
      store.dispatch('addEvents', response);
    },
    msToDate(ms){
        var d = new Date(ms);
        return d.getFullYear() + '-' + ("0" + (d.getMonth() + 1)).slice(-2) + '-' + ("0" + d.getDate()).slice(-2);
    },
    promiseAll:  (promises) => {
        promises.map(promise => promise.catch(err => 
          console.log(err)))
        return Promise.all(promises)
    },
    async getStoreUsersAvailabilities(){
        await Promise.all([this.getStoreInfos(), this.getUserInfos(), this.getDateRangeAvailabilities()])
        .then(res => {
            console.log(res)
        })
        .catch(err => {
            console.error(err)
        })
    },
    eventsMapFactory(){
      let events = store.state.events;

      const map = {}
        
      this.events.forEach(function (e){
        var d = new Date(e.localStoreStartDateTime);

        let tempDate = d.getFullYear() + '-' + ((d.getMonth()+1) > 9 ? (d.getMonth() + 1) : '0' + (d.getMonth() + 1)) + '-' + (d.getDate() > 9 ? d.getDate() : '0' + d.getDate());
        let tempHour = d.getHours() + ':00';
        
        (map[tempDate][tempHour] = map[tempDate][tempHour] || []).push(e)
      })

      return map;
    },
    arrayUnique(array) {
      var a = array.concat();
      for(var i=0; i<a.length; ++i) {
          for(var j=i+1; j<a.length; ++j) {
              if(a[i] === a[j])
                  a.splice(j--, 1);
          }
      }
      return a;
    },
}
</script>


