<template>
    <modal name="confirmDeleteModal"        
        width="30%"
        height="auto"
    >
        <transition name="modal">
            <div class="delete-modal-mask">
                <div class="delete-modal-wrapper">
                    <div class="delete-modal-container">

                        <div class="delete-modal-body">
                          Please confirm that you'd like to delete this Availability.
                        </div>

                        <div class="delete-modal-footer">
                             <button @click="closeModal()" class="eventTypeBtn delete-btn">
                                Cancel 
                            </button>
                            <button @click="confirmDeleteAvailability()" class="eventTypeBtn">
                               <div v-if="!showLoader"> Confirm </div>
                            <div class="lds-spinner" v-if="showLoader"><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div></div>

                            </button>
                        </div>
                        
                    </div>
                </div>
            </div>
        </transition>

    </modal>
</template>

<script>

export default {
  name: 'confirmDeleteModal',
  components: {
    
  },
  data () {
    return {
        showLoader: false,

    }
  },
  methods:{
        closeModal(){
        this.$modal.hide('confirmDeleteModal');
      },
      confirmDeleteAvailability(){
        this.showLoader = true
    	this.$emit('confirmDeleteAvailability');
    }
  },
  computed:{
    
  },
     
}
</script>
<style>

.delete-modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .1);
  display: table;
  transition: opacity .3s ease;
}

.delete-modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}

.delete-modal-container {
  width: 479px;
  margin: 0px auto;
  padding: 34px 79px;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
  transition: all .3s ease;
  text-align: center;
}

.delete-modal-body {
  font-size: 17px;
  font-weight: 300;
  line-height: 1.18;
  color: #000000;
  margin-bottom: 32px
}

.delete-modal-footer {
    display: flex;
    flex-wrap: nowrap;
}

.modal-enter {
  opacity: 0;
}

.modal-leave-active {
  opacity: 0;
}

.modal-enter .modal-container,
.modal-leave-active .modal-container {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}

</style>


